.forfait-card {
  padding: 20px;
  border-radius: 10px;
  border: 1px solid #e0e0e0;
  margin-bottom: 20px;
  transition: all 0.3s ease;

  &.selected {
    background-color: var(--ion-color-primary);
    color: var(--ion-color-primary-contrast);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);

    .title,
    .info-block .description,
    .link {
      color: inherit;
    }
  }

  .flex-between {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .title_price {
      white-space: nowrap;
    }
  }

  .title {
    font-weight: bold;
    margin: 0;
    font-size: 18px;
  }

  .info-block {
    margin-top: 10px;

    .info-item {
      margin-top: 3px;
      &:last-child {
        margin-bottom: 0;
      }
    }

    .description {
      font-size: 0.8em;
      color: #666;
    }
  }

  .link {
    display: inline-block;
    margin-top: 10px;
    cursor: pointer;
    text-decoration: underline;
    color: var(--ion-color-dark);
  }
}

ion-footer ion-toolbar {
  --padding-top: 0;
  --padding-bottom: 0;
  --min-height: auto;

  ion-button {
    margin: 10px;
  }

  ion-text {
    display: block;
    text-align: center;
    font-size: 14px;
    margin-bottom: 10px;
  }
}
